export class Meal {
  id: string;
  user_id: string;
  date: string;
  dateObject?: Date;
  name: string;
  weight: number;
  calories: number;
  proteins: number;
  fats: number;
  carbohydrates: number;

  addDate() {
    this.dateObject = new Date();
    this.date = this.dateObject.toDateString();
  }
}



