import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { HeaderComponent } from './components/header/header.component';
import { MainComponent } from './components/main/main.component';
import { LoginComponent } from './components/authorization/login/login.component';
import { SignupComponent } from './components/authorization/signup/signup.component';
import { HareComponent } from './components/hare/hare.component';
import { UserService } from './services/user-service/user.service';
import { AuthGuardService } from './services/auth-guard.service';
import { AppRoutingModule } from './router/app-routing.module';
import { AngularFireDatabaseModule } from 'angularfire2/database';
import { AngularFireAuthModule } from 'angularfire2/auth';
import { AngularFirestoreModule } from 'angularfire2/firestore';
import { FormsModule } from '@angular/forms';
import { environment } from '../environments/environment';
import { DisplayErrFormDirective } from './directives/display_err_form.directive';
import { PositiveValidator } from './directives/positive-validator.directive';
import { NotFoundComponent } from './components/not-found/not-found/not-found.component';
import { SummaryPopupComponent } from './components/authorization/summary-popup/summary-popup.component';
import { UserInfoComponent } from './components/user-info/user-info.component';
import { LoadingSignComponent } from './components/loading-sign/loading-sign.component';
import { TabsComponent } from './components/tabs/tabs.component';
import { StatisticComponent } from './components/statistic/statistic.component';
import { StatisticService } from './services/statistic-service/statistic.service';
import { ProductService } from './services/product-service/product.service';
import { AngularFireModule } from 'angularfire2';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { EmailIsFreeValidator } from './directives/email-is-free-validator.directive';
import { AddProductComponent } from './components/my-products/my-products.component';
import { ModalProductsComponent } from './components/modal-products/modal-products.component';
import { BsDropdownModule } from 'ngx-bootstrap/dropdown';
import { TooltipModule } from 'ngx-bootstrap/tooltip';
import { ModalModule } from 'ngx-bootstrap/modal';
import { ReactiveFormsModule } from '@angular/forms';
import { DailyProgressService } from './services/daily-progress-service/daily-progress.service';
import { EditUserDataComponent } from './components/edit-user-data/edit-user-data.component';
import { ImageCropperModule } from 'ng2-img-cropper';
import { TodayComponent } from './components/today/today.component';
import { MealService } from './services/meal-service/meal.service';
import { ProgressInfoComponent } from './components/progress-info/progress-info.component';
import { WaterGlassComponent } from './components/water-glass/water-glass.component';
import { SearchPipe } from './components/today/search/search-weight.pipe';
import { SearchComponent } from './components/today/search/search-weight.component';
import { CreateDishComponent } from './components/dishes/create-dish/create-dish.component';
import { DishesComponent } from './components/dishes/dishes.component';
import { DishService } from './services/dish-service/dish.service';


@NgModule({
  declarations: [
    AppComponent,
    SignupComponent,
    EmailIsFreeValidator,
    PositiveValidator,
    DisplayErrFormDirective,
    AppComponent,
    HeaderComponent,
    MainComponent,
    SummaryPopupComponent,
    LoginComponent,
    SignupComponent,
    HareComponent,
    NotFoundComponent,
    UserInfoComponent,
    SummaryPopupComponent,
    LoadingSignComponent,
    StatisticComponent,
    AddProductComponent,
    ModalProductsComponent,
    TabsComponent,
    EditUserDataComponent,
    EditUserDataComponent,
    TodayComponent,
    ProgressInfoComponent,
    WaterGlassComponent,
    SearchPipe,
    SearchComponent,
    CreateDishComponent,
    DishesComponent
  ],
  imports: [
    ImageCropperModule,
    BrowserModule,
    BrowserAnimationsModule,
    FormsModule,
    NgbModule.forRoot(),
    AngularFireModule.initializeApp(environment.firebase),
    AngularFirestoreModule,
    AngularFireAuthModule,
    AngularFireDatabaseModule,
    AppRoutingModule,
    ModalModule,
    TooltipModule,
    BsDropdownModule,
    ReactiveFormsModule
  ],
  providers: [
    UserService,
    ProductService,
    StatisticService,
    AppRoutingModule,
    ReactiveFormsModule,
    DailyProgressService,
    ProductService,
    MealService,
    AuthGuardService,
    DishService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
export class AppBootstrapModule {}



