import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection } from 'angularfire2/firestore';
import { CollectionReference, Query } from '@firebase/firestore-types/index';
import { DailyProgress } from '../../models/daily_progress';
import 'rxjs/add/operator/first';

@Injectable()
export class StatisticService {
  statCollection: AngularFirestoreCollection<DailyProgress>;

  constructor(private afs: AngularFirestore) { }
  getProgress (userID: string, startDate: number) {
    return this.afs.collection('daily_progress', (ref) => {
      let query: CollectionReference | Query = ref;
      if (userID) {
        query = query.where('user_id', '==', userID)
          .where('_date', '>=', startDate);
      }
      return query;
    }).valueChanges()
      .map(data => data.map(item => Object.assign(new DailyProgress(), item)));
  }

}



