export class Product {
  id: string;
  name: string;
  calories: number;
  proteins: number;
  fats: number;
  carbohydrates: number;
  user_id?: string;
  favorite: boolean;

  constructor(user_id?: string, name?: string) {
    this.user_id = user_id;
    this.name = name;
  }
}



