import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument  } from 'angularfire2/firestore';
import { CollectionReference, Query } from '@firebase/firestore-types/index';
import { UserService } from '../user-service/user.service';
import { Meal } from '../../models/meal';
import { User } from '../../models/user';

@Injectable()
export class MealService {
  date = new Date ();
  user: User;

  constructor(private afs: AngularFirestore, private userService: UserService) { }

  getMeals (userID: string, currentDate: string): AngularFirestoreCollection<Meal> {
    return this.afs.collection('meals_history', (ref) => {
      let query: CollectionReference | Query = ref;
      if (userID) {
        query = query.where('user_id', '==', userID)
          .where('date', '==', currentDate);
      }
      return query;
    });
  }

  addMealToDB (newMeal: Meal): Promise<void> {
    newMeal.id = this.afs.createId();
    this.userService.loggedUser.subscribe( res => {
      this.user = res;
    });
    newMeal.user_id = this.user.id;
    newMeal.addDate();
    return this.afs.collection('meals_history').doc(newMeal.id)
      .set(Object.assign({}, newMeal));
  }

  deleteMealFromDB (meal: Meal): void {
    this.afs.doc(`meals_history/${meal.id}`).delete();
  }

  updateMealDB(meal: Meal) {
    return this.afs.doc<Meal>(`meals_history/${meal.id}`).update(Object.assign({}, meal));
  }
}



