import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { LoginComponent } from '../components/authorization/login/login.component';
import { SignupComponent } from '../components/authorization/signup/signup.component';
import { Routes, RouterModule } from '@angular/router';
import { NotFoundComponent } from '../components/not-found/not-found/not-found.component';
import { MainComponent } from '../components/main/main.component';
import { AuthGuardService } from '../services/auth-guard.service';
import { SummaryPopupComponent } from '../components/authorization/summary-popup/summary-popup.component';
import { StatisticComponent } from '../components/statistic/statistic.component';
import { EditUserDataComponent } from '../components/edit-user-data/edit-user-data.component';
import { AddProductComponent } from '../components/my-products/my-products.component';
import { ModalProductsComponent } from '../components/modal-products/modal-products.component';
import { TodayComponent } from '../components/today/today.component';
import { CreateDishComponent } from '../components/dishes/create-dish/create-dish.component';
import { DishesComponent } from '../components/dishes/dishes.component';

const authRoutes: Routes = [
  { path: 'today', component: TodayComponent },
  { path: 'my_products', component: AddProductComponent },
  { path: 'my_dishes', component: DishesComponent },
  { path: 'statistic', component: StatisticComponent },
];


const appRoutes: Routes = [
  { path: '', redirectTo: '/login', pathMatch: 'full' },
  { path: 'main', redirectTo: '/main/today', pathMatch: 'full' },
  { path: 'login', component: LoginComponent },
  { path: 'signup', component: SignupComponent },
  { path: 'main', component: MainComponent, canActivate: [AuthGuardService], children: authRoutes },
  { path: 'summary', component: SummaryPopupComponent, outlet: 'popup' },
  { path: 'edit-info', component: EditUserDataComponent, outlet: 'popup' },
  { path: 'add-products', component: ModalProductsComponent, outlet: 'popup' },
  { path: 'create-dish', component: CreateDishComponent, outlet: 'popup' },
  { path: '**', component: NotFoundComponent }
];

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forRoot(appRoutes)
  ],
  exports: [
    RouterModule
  ],
  declarations: []
})
export class AppRoutingModule {
}



