import { Component, OnInit } from '@angular/core';
import { UserService } from '../../services/user-service/user.service';
import { StatisticService } from '../../services/statistic-service/statistic.service';
import { Observable } from 'rxjs/Observable';
import { Chart } from 'chart.js';
import { User } from '../../models/user';
import { DailyProgressService } from '../../services/daily-progress-service/daily-progress.service';
import { DailyProgress } from '../../models/daily_progress';


@Component({
  selector: 'app-statistic',
  templateUrl: './statistic.component.html',
  styleUrls: ['./statistic.component.scss']
})
export class StatisticComponent implements OnInit {
  user: User;
  statics: DailyProgress[];
  chart = [];
  chartWater = [];
  statDays = 7;

  constructor(private statisticService: StatisticService, private userService: UserService) { }

  ngOnInit() {
    this.userService.loggedUser.subscribe( res => {
      this.user = res;
    });
    this.getStat().subscribe(stat => {
      this.statics = stat;
      // chart create
      this.initChart();
      this.initChartWater();
    });
  }

  private getStat(): Observable<DailyProgress[]> {
    const dateStartStatistic = this.getDateAgo(this.statDays - 1);
    return this.statisticService.getProgress(this.user.id, dateStartStatistic).map(stat => {
      // if this.statics array doesn`t have some dates - add zero progress with those dates
      for (let i = 0; i < this.statDays; i++) {
        const date = this.getDateAgo(i);
        if (stat.some(progress => progress.date === date)) {
          continue;
        }
        const zeroProgress = new DailyProgress();
        zeroProgress.date = date;
        zeroProgress.user_id = this.user.id;
        stat.push(zeroProgress);
      }
      return stat.sort((a, b) => b.date - a.date);
    });
  }

  private getDateAgo(days: number): number {
    const date = new Date();
    return +(new Date(date.getFullYear(), date.getMonth(), date.getDate() - days));
  }

  private getMaxOfArray (numArray) {
    return Math.max.apply(null, numArray);
  }

  initChart(): void {
    this.chart = new Chart('canvas', {
      type: 'line',
      data: {
        labels: this.statics.map( res =>  new Date(res.date).getDate() + '.' + Number(new Date(res.date).getMonth() + 1))
          .reverse(),
        datasets: [{
          label: 'Proteins',
          data: this.statics.map(res => res.proteins).reverse(),
          borderColor: ['#C10008'],
          backgroundColor: 'rgba(193, 0, 8, 0.7)',
          borderWidth: 2,
          fill: false
        },
          {
            label: 'Fats',
            data: this.statics.map(res => res.fats).reverse(),
            borderColor: ['#EBA82A'],
            backgroundColor: 'rgba(235, 168, 42, 0.7)',
            borderWidth: 2,
            fill: false
          },
          {
            label: 'Carbs',
            data: this.statics.map(res => res.carbohydrates).reverse(),
            borderColor: ['#409292'],
            backgroundColor: 'rgba(64, 146, 146, 0.7)',
            borderWidth: 2,
            fill: false
          }]
      },
      options: {
        tooltips: {
          enabled: false
        },
        title: {
          fontSize: 25,
          fontColor: '#C10008',
          fontFamily: '\'Courgette\', cursive',
          display: true,
          text: 'Nutrition Statistic for last 7 days'
        },
        legend: {
          position: 'bottom',
          display: true
        },
        scales: {
          xAxes: [{
            display: true
          }],
          yAxes: [{
            display: true,
            ticks: {
              stepSize: 40,
              beginAtZero: true,
              suggestedMax: this.getMaxOfArray(this.statics.map(res => res.carbohydrates).reverse())
            }
          }]
        }
      }
    });
  }

  initChartWater(): void {
    this.chartWater = new Chart('canvasWater', {
      type: 'line',
      data: {
        labels: this.statics.map( res => new Date(res.date).getDate() + '.' + Number(new Date(res.date).getMonth() + 1))
          .reverse(),
        datasets: [{
          label: 'Calories',
          data: this.statics.map(res => res.calories).reverse(),
          backgroundColor: 'rgba(193, 0, 8, 0.7)',
          borderColor: ['#C10008'],
          borderWidth: 2,
          fill: false
        },
          {
            label: 'Water',
            data: this.statics.map(res => res.water).reverse(),
            backgroundColor: 'rgba(64, 146, 146, 0.7)',
            borderColor: ['#409292'],
            borderWidth: 2,
            fill: false
          }]
      },
      options: {
        legend: {
          position: 'bottom',
          display: true
        },
        tooltips: {
          enabled: false
        },
        title: {
          fontSize: 25,
          fontColor: '#C10008',
          fontFamily: '\'Courgette\', cursive',
          display: true,
          text: 'Calories and Water statistic'
        },
        scales: {
          xAxes: [{
            display: true
          }],
          yAxes: [{
            display: true,
            ticks: {
              stepSize: 500,
              beginAtZero: true,
              suggestedMax: this.getMaxOfArray(this.statics.map(res => res.calories)
                .concat(this.statics.map(res => res.water)))
            }
          }]
        }
      }
    });
  }
}



