import {Component, OnInit} from '@angular/core';
import {UserService} from '../../services/user-service/user.service';
import {DailyProgressService} from '../../services/daily-progress-service/daily-progress.service';
import {DailyProgress} from '../../models/daily_progress';
import {User} from '../../models/user';
import * as ProgressBar from 'progressbar.js/dist/progressbar.js';

@Component({
  selector: 'app-progress-info',
  templateUrl: './progress-info.component.html',
  styleUrls: ['./progress-info.component.scss']
})
export class ProgressInfoComponent implements OnInit {
  current: DailyProgress;
  maxKPCF = {
    maxCalories: 0,
    maxFats: 0,
    maxCarbo: 0,
    maxProt: 0,
    maxWater: 0
  };
  user: User;
  userID: string;
  calories: ProgressBar;
  carbs: ProgressBar;
  proteins: ProgressBar;
  fats: ProgressBar;
  water: ProgressBar;
  addedKPCF = {
    addedCalories: 0,
    addedWater: 0,
    addedCarbs: 0,
    addedProt: 0,
    addedFat: 0
  };
  titleKPCF = {
    caloriesTitle: '',
    carboTitle: '',
    proteinsTitle: '',
    fatsTitle: '',
    waterTitle: ''
  };
  isDisplayed = true;

  constructor(private userService: UserService, private dailyService: DailyProgressService) {
  }

  ngOnInit() {
    this.userService.loggedUser.subscribe(res => {
      this.user = res;
      this.userID = this.user.id;
      this.maxKPCF.maxCalories = this.user.kpcf.kilocalories;
      this.maxKPCF.maxCarbo = this.user.kpcf.carbohydrates;
      this.maxKPCF.maxFats = this.user.kpcf.fats;
      this.maxKPCF.maxProt = this.user.kpcf.proteins;
      this.maxKPCF.maxWater = this.user.weight * 30;
    });
    this.dailyService.observeProgress(this.userID, Date.now()).subscribe(current => {
      this.current = current;
      this.addedKPCF.addedCalories = this.current.calories;
      this.addedKPCF.addedWater = this.current.water;
      this.addedKPCF.addedCarbs = this.current.carbohydrates;
      this.addedKPCF.addedProt = this.current.proteins;
      this.addedKPCF.addedFat = this.current.fats;
      this.createProgressBars();
    });
  }

  createProgressBars(): void {
    if (this.isDisplayed) {
      this.calories = new ProgressBar.Circle('.progress-bar-calories', {
        strokeWidth: 10,
        easing: 'easeInOut',
        duration: 1400,
        color: '#C10008',
        trailColor: '#DDE3E8',
        trailWidth: 10,
        svgStyle: {width: '100%', height: '100%'},
        step: (state, bar) => {
          this.titleKPCF.caloriesTitle = Math.round(bar.value() * 100) + ' %';
        }
      });
      this.carbs = new ProgressBar.Line('.progress-bar-carbohydrates', {
        strokeWidth: 5,
        easing: 'easeInOut',
        duration: 1400,
        color: 'rgba(64, 146, 146, 0.5)',
        trailColor: '#DDE3E8',
        trailWidth: 5,
        svgStyle: {width: '100%', height: '20px'},
        step: (state, bar) => {
          this.titleKPCF.carboTitle = Math.round(bar.value() * 100) + ' %';
        }
      });
      this.fats = new ProgressBar.Line('.progress-bar-fats', {
        strokeWidth: 6,
        easing: 'easeInOut',
        duration: 1400,
        color: 'rgba(235, 168, 42, 0.5)',
        trailColor: '#DDE3E8',
        trailWidth: 6,
        svgStyle: {width: '100%', height: '20px'},
        step: (state, bar) => {
          this.titleKPCF.fatsTitle = Math.round(bar.value() * 100) + ' %';
        }
      });
      this.proteins = new ProgressBar.Line('.progress-bar-proteins', {
        strokeWidth: 6,
        easing: 'easeInOut',
        duration: 1400,
        color: 'rgba(193, 0, 8, 0.5)',
        trailColor: '#DDE3E8',
        trailWidth: 6,
        svgStyle: {width: '50%', height: '20px'},
        step: (state, bar) => {
          this.titleKPCF.proteinsTitle = Math.round(bar.value() * 100) + ' %';
        }
      });
      this.water = new ProgressBar.SemiCircle('.progress-bar-water', {
        strokeWidth: 10,
        easing: 'easeInOut',
        duration: 1400,
        color: '#4077B3',
        trailColor: '#DDE3E8',
        trailWidth: 10,
        svgStyle: {width: '100%', height: '100%'},
        step: (state, bar) => {
          this.titleKPCF.waterTitle = Math.round(bar.value() * 100) + ' %';
        }
      });
      this.isDisplayed = false;
    }
    this.calories.animate(this.addedKPCF.addedCalories / this.maxKPCF.maxCalories);
    this.carbs.animate(this.addedKPCF.addedCarbs / this.maxKPCF.maxCarbo);
    this.fats.animate(this.addedKPCF.addedFat / this.maxKPCF.maxFats);
    this.proteins.animate(this.addedKPCF.addedProt / this.maxKPCF.maxProt);
    this.water.animate(this.addedKPCF.addedWater / (this.user.weight * 30));
  }

}



