import { Component, OnInit } from '@angular/core';
import { UserService } from '../../../services/user-service/user.service';
import { Router } from '@angular/router';
import { User } from '../../../models/user';
import { NgbDatepickerConfig } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.scss'],
  providers: [ NgbDatepickerConfig ]
})
export class SignupComponent implements OnInit {
  user: User;
  submitted: boolean;

  // validation messages
  msgs = {
    REQUIRED: 'This field is required',
    TAKEN_EMAIL: 'This email was already registered',
    EMAIL_INCORRECT: 'Input the correct email address',
    PWD_INCORRECT: 'The password must consist only of Latin letters or numbers',
    TOO_SHORT_PWD: 'The password must be at least 6 characters long',
    PWDS_DISAGREE: 'Doesn\'t match the password',
    NOT_POSITIVE: 'This value must be positive'
  };

  constructor(
    private userService: UserService,
    private router: Router,
    datapickerConfig: NgbDatepickerConfig
    ) {

    const date = new Date();
    datapickerConfig.minDate = {year: 1900, month: 1, day: 1};
    datapickerConfig.maxDate = {
      year: date.getFullYear(),
      month: date.getMonth() + 1,
      day: date.getDate()
    };
  }

  ngOnInit() {
    this.user = new User;
    this.user.gender = 0;
    this.user.activity = 0;

    this.submitted = false;
  }

  submit(form): void {
    if (form.invalid) {

      return;
    }
    this.submitted = true;
    const serviceResponse = this.userService.createUser(this.user);
    serviceResponse.subscribe(
      () => this.router.navigate([{ outlets: { popup: 'summary' }}]),
      () => console.log('Something wrong: the database error')
    );
  }
}



