import { Component, OnInit } from '@angular/core';
import { UserService } from '../../services/user-service/user.service';
import { DailyProgressService } from '../../services/daily-progress-service/daily-progress.service';
import { User } from '../../models/user';
import { DailyProgress } from '../../models/daily_progress';


@Component({
  selector: 'app-water-glass',
  templateUrl: './water-glass.component.html',
  styleUrls: ['./water-glass.component.scss']
})
export class WaterGlassComponent implements OnInit {
  public user: User;
  public progress: DailyProgress;

  constructor(private userService: UserService, private dailyProgressService: DailyProgressService) {
  }

  ngOnInit() {
    this.userService.loggedUser.subscribe(loggedUser => this.user = loggedUser);
    this.dailyProgressService.observeProgress(this.user.id, Date.now())
      .subscribe(progress => this.progress = progress);

  }

  updateWater(count) {
    const updatedProgress = new DailyProgress();
    updatedProgress.water += count;
    updatedProgress.date = Date.now();
    updatedProgress.user_id = this.user.id;
    this.dailyProgressService.up(updatedProgress);
  }
}



