import { trigger, style, transition, animate, state } from '@angular/animations';

export let showMessage = trigger('showMessage', [
  state('default', style({opacity: 0})),
  state('show', style({opacity: 1})),
    transition('default => show', [
      animate('500ms 2s')
    ]),
  ]);

export let showHare = trigger('showHare', [
  state('default', style({opacity: 0})),
  state('show', style({opacity: 1})),
  transition('default => show', [
    animate('500ms 1s')
  ]),
]);



