import { Gender } from './enums/gender';
import { Activity } from './enums/activity';
import { KPCF } from './kpcf';

export class User {
  public id?: string;
  public email: string;
  public password: string;
  public name: string;
  public photoURL?: string;
  private _gender: Gender;
  // depends on what is received from signup.component.ts
  private _birthdate: Date | object;
  private _age?: number;
  private _height: number;
  private _weight: number;
  private _activity: Activity;
  private _kpcf?: KPCF;

  constructor(email?: string, password?: string) {
    this.email = email;
    this.password = password;
  }

  set birthdate(inputDate: any) {
    const thisYear = new Date().getFullYear();
    // invalid input date
    if (thisYear < inputDate.year) {
      return;
    }
    this._birthdate = inputDate;
    this._age = thisYear - inputDate.year;
    this.calculateKPCF();
  }

  get birthdate() {
    return this._birthdate;
  }

  set gender(inputGender: string | number) {
    // inputGender comes from sugnup form as a string '0' or '1'
    this._gender = (typeof inputGender === 'string') ?
      parseInt(inputGender, 10) : inputGender;
    this.photoURL = this.photoURL || '../../assets/img/' +
      (this._gender === Gender.male.valueOf() ? 'man' : 'woman') + '.jpg';
    this.calculateKPCF();
  }

  get gender() {
    return this._gender;
  }

  set weight(inputWeight: number) {
    this._weight = inputWeight;
    this.calculateKPCF();
  }

  get weight() {
    return this._weight;
  }

  set height(inputHeight: number) {
    this._height = inputHeight;
    this.calculateKPCF();
  }

  get height() {
    return this._height;
  }

  set activity(inputActivity: string | number) {
    // inputActivity comes from sugnup form as a string '0' or '1'...
    this._activity = (typeof inputActivity === 'string') ?
      parseInt(inputActivity, 10) : inputActivity;
    this.calculateKPCF();
  }

  get activity() {
    return this._activity;
  }

  calculateKPCF() {
    if (typeof this._age === 'undefined' || typeof this._height === 'undefined' ||
        typeof this._weight === 'undefined' || typeof this._gender === 'undefined' ||
        typeof this._activity === 'undefined') {

      return;
    }

    let activityCoeff: number;
    let metabolismValue: number;
    let kilocalories: number;
    let proteins: number;
    let carbohydrates: number;
    let fats: number;

    if (this._activity === Activity.low.valueOf()) {
      activityCoeff = 1.2;
    } else if (this._activity === Activity.medium.valueOf()) {
      activityCoeff = 1.4;
    } else if (this._activity === Activity.high.valueOf()) {
      activityCoeff = 1.46;
    }

    if (this._gender === Gender.male.valueOf()) {
      metabolismValue = 66.47 + this._weight * 13.75 + this._height * 5 - this._age * 6.74;
    } else if (this._gender === Gender.female.valueOf()) {
      metabolismValue = 665.1 + this._weight * 9.6 + this._height * 1.85 - this._age * 4.68;
    }

    kilocalories = Math.round(metabolismValue * activityCoeff);
    proteins = Math.round(kilocalories * 0.2 / 4);
    carbohydrates = Math.round(kilocalories * 0.5 / 4);
    fats = Math.round(kilocalories * 0.3 / 9);

    this._kpcf = {
      kilocalories: kilocalories,
      proteins: proteins,
      carbohydrates: carbohydrates,
      fats: fats
    };

    return this._kpcf;
  }
  get kpcf() {
    return this._kpcf;
  }
}



