import { Directive, forwardRef } from '@angular/core';
import { AbstractControl, NG_ASYNC_VALIDATORS, Validator } from '@angular/forms';
import { UserService } from '../services/user-service/user.service';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/first';

@Directive({
  selector: '[appEmailIsFree]',
  providers: [{
    provide: NG_ASYNC_VALIDATORS,
    useExisting: forwardRef(() => EmailIsFreeValidator),
    multi: true
  }]
})
export class EmailIsFreeValidator implements Validator {
  constructor(private userService: UserService) { }

  validate(control: AbstractControl): Observable<{[key: string]: any}> {
    const serviceResponse = this.userService.getUser(control.value);

    return serviceResponse.map(user => {

      return user ? {'takenEmail': {value: control.value}} : null;
    }).first();
  }
}



