import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection } from 'angularfire2/firestore';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/merge';
import { Product } from '../../models/product';
import { CollectionReference, Query } from '@firebase/firestore-types/index';
import { Dish } from '../../models/dish';

@Injectable()
export class ProductService {
  products: Observable<Product[]>;
  dishes: Observable<Dish[]>;
  productsFavorite: Observable<string[]>;

  constructor(private afs: AngularFirestore) {}

  addProduct(newProduct: Product) {
    newProduct.id = this.afs.createId();
    const productDoc = this.afs.collection('products').doc<Product>(newProduct.id);
    productDoc.set(Object.assign({}, newProduct));

    return productDoc.valueChanges();
  }

  deleteProduct(product: Product) {
    this.afs.doc(`products/${product.id}`).delete();
  }

  getProducts (userID: string): Observable<Product[]> {
    const productCollection = this.afs.collection<Product[]>('products', (ref) => {
      let query: CollectionReference | Query = ref;
      if (userID) {
        query = query.where('user_id', '==', userID);
      }
      return query;
    });

    this.products = productCollection.snapshotChanges().map(changes => {
      return changes.map(a => {
        const data = a.payload.doc.data() as Product;
        data.id = a.payload.doc.id;
        return data;
      });
    });
    return this.products;
  }

  getProductsCommon (adminID: string): Observable<Product[]> {
    const productCollection = this.afs.collection<Product[]>('products', (ref) => {
      let query: CollectionReference | Query = ref;
      if (adminID) {
        query = query.where('user_id', '==', 'admin');
      }
      return query;
    });

    this.products = productCollection.snapshotChanges().map(changes => {
      return changes.map(a => {
        const data = a.payload.doc.data() as Product;
        data.id = a.payload.doc.id;
        return data;
      });
    });

    return this.products;
  }

  getDishes (userID: string): Observable<Dish[]> {
    const DishCollection = this.afs.collection<Product[]>('dishes', (ref) => {
      let query: CollectionReference | Query = ref;
      if (userID) {
        query = query.where('user_id', '==', userID);
      }
      return query;
    });

    this.dishes = DishCollection.snapshotChanges().map(changes => {
      return changes.map(a => {
        const data = a.payload.doc.data() as Dish;
        data.id = a.payload.doc.id;
        return data;
      });
    });

    return this.dishes;
  }

  getProductsConcat(user, admin) {
    const one = this.getProducts(user);
    const two = this.getProductsCommon(admin);
    return two.merge(one);
  }

  getFavoriteProducts (userID: string) {
    const productCollection = this.afs.collection<string>('favorites/' + userID + '/ids', (ref) => {
      const query: CollectionReference | Query = ref;
      return query;
    });

    this.productsFavorite = productCollection.snapshotChanges().map(changes => {
      return changes.map(a => {
        const data = a.payload.doc.id as string;
        return data;
      });
    });

    return this.productsFavorite;
  }

  switchFavoriteProducts (userId, id, added) {
    const ref = this.afs.collection('favorites/' + userId + '/ids').doc(id);
    if (added) {
      ref.set({});
    } else {
      ref.delete();
    }
  }
}



