import { Injectable } from '@angular/core';
import { AngularFirestore, DocumentChangeAction } from 'angularfire2/firestore';
import { DailyProgress } from '../../models/daily_progress';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Subscription } from 'rxjs/Subscription';
import 'rxjs/add/operator/first';

@Injectable()
export class DailyProgressService {

  private progress = new BehaviorSubject(new DailyProgress());
  private progressSubscr: Subscription;

  constructor(private afs: AngularFirestore) { }

  // increases daily progress
  up(plus: DailyProgress): void {
    if (!plus.user_id || !plus.date) {
      console.log('Error: no user id or date to add the progress!');

      return;
    }
    this.getSnapshot(plus.user_id, plus.date).subscribe((snapshot) => {
      if (!snapshot) {
        // if the user hasn't yet started the daily progress - create it
        this.create(plus);

        return;
      }
      // else - add new part of the progress
      const progress = this.getProgressFromSnapshot(snapshot);
      progress.add(plus);
      this.update(progress);
    });
  }

  // reduces daily progress
  down(minus: DailyProgress): void {
    if (!minus.user_id || !minus.date) {
      console.log('Error: no user id or date to reduce the progress!');

      return;
    }
    this.getSnapshot(minus.user_id, minus.date).subscribe((snapshot) => {
      if (!snapshot) {
        console.log('Error: nothing to delete!');

        return;
      }
      const progress = this.getProgressFromSnapshot(snapshot);
      if (!progress.reduce(minus)) {
        // if the "minus" is greater than the existing progress - error
        console.log('Error: incorrect deleting!');

        return;
      }
      this.update(progress);
    });
  }

  // updates and returns the progress BehaviorSubject
  observeProgress(userID: string, date: number): BehaviorSubject<DailyProgress> {
    // rounding the date to match with the database records
    const d = new Date(date);
    const cleanDate = +(new Date(d.getFullYear(), d.getMonth(), d.getDate()));

    if (this.progressSubscr) {
      this.progressSubscr.unsubscribe();
    }

    this.progressSubscr = this.getCollect(userID, cleanDate).valueChanges()
      .subscribe(collect => {
        this.progress.next(Object.assign(new DailyProgress(), collect[0]));
      });

    return this.progress;
  }

  // creates a new progress record in the database
  create(progress: DailyProgress): void {
    const id = this.afs.createId();
    const progressDoc = this.afs.doc(`daily_progress/${id}`);
    progressDoc.set(Object.assign({}, progress));
  }

  // updates the progress record in the database
  private update(progress: DailyProgress): void {
    const progressDoc = this.afs.doc(`daily_progress/${progress.id}`);
    progressDoc.update(Object.assign({}, progress));
  }

  // converts a progress snapshot to the DailyProgress model and returns it
  private getProgressFromSnapshot(snapshot: DocumentChangeAction): DailyProgress {
    const progress = new DailyProgress();
    Object.assign(progress, snapshot.payload.doc.data());
    progress.id = snapshot.payload.doc.id;

    return progress;
  }

  // gets a progress snapshot record (with metadata) from the database
  private getSnapshot(userID: string, date: number) {

    return this.getCollect(userID, date)
      .snapshotChanges()
      .map((collect) => collect[0])
      .first();
  }

  // creates a collection of the progress records by user id and date
  private getCollect(userID: string, date: number) {

    return this.afs.collection('daily_progress', (ref) => {

      return ref.where('user_id', '==', userID).where('_date', '==', date);
    });
  }
}



