import { Component, OnInit, ViewChild } from '@angular/core';
import { UserService } from '../../services/user-service/user.service';
import { User } from '../../models/user';
import { MealService } from '../../services/meal-service/meal.service';
import { Meal } from '../../models/meal';
import { DailyProgressService } from '../../services/daily-progress-service/daily-progress.service';
import { DailyProgress } from '../../models/daily_progress';
import { SearchComponent } from './search/search-weight.component';

@Component({
  selector: 'app-today',
  templateUrl: './today.component.html',
  styleUrls: ['./today.component.scss']
})

export class TodayComponent implements OnInit {

  @ViewChild(SearchComponent) SearchComponent: SearchComponent;

  meals: Meal[];
  user: User;
  userID: string;
  todayDate: Date;
  currentDate: string;
  showMessage: boolean;

  constructor(private mealService: MealService,
              private progressService: DailyProgressService,
              private userService: UserService) { }

  ngOnInit() {
    this.userService.loggedUser.subscribe( res => {
      this.user = res;
      this.userID = this.user.id;
    });
    this.todayDate = new Date();
    this.currentDate = this.todayDate.toDateString();

    this.mealService.getMeals(this.userID, this.currentDate).valueChanges().subscribe(meals => {
      this.meals = meals;
      this.meals.sort((a, b) => {
        if (a.dateObject < b.dateObject) {
          return -1;
        }
        if (a.dateObject > b.dateObject) {
          return 1;
        }
        return 0;
      });
      this.meals.length === 0 ? this.showMessage = true : this.showMessage = false;
    });
  }

  getProgress(meal: Meal): DailyProgress {
    const progress = new DailyProgress();
    progress.user_id = this.userID;
    progress.calories = meal.calories;
    progress.proteins = meal.proteins;
    progress.fats = meal.fats;
    progress.carbohydrates = meal.carbohydrates;
    progress.date = Date.now();

    return progress;
  }

  open() {
    this.SearchComponent.open();
  }

  deleteMeal( meal: Meal) {
    this.mealService.deleteMealFromDB(meal);
    this.progressService.down(this.getProgress(meal));
  }

  // part EditMeal Biriukova
  openEdit(meal: Meal) {
    this.SearchComponent.openEdit(meal, meal.id);
  }
}




