export class Dish {
  id: string;
  name: string;
  calories = 0;
  proteins = 0;
  fats = 0;
  carbohydrates = 0;
  user_id?: string;
  ingredients?: Array<object>;

  constructor(user_id?: string, name?: string) {
    this.user_id = user_id;
    this.name = name;
  }
}



