import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';

import { UserService } from '../../../services/user-service/user.service';
import { ProductService } from '../../../services/product-service/product.service';
import { MealService } from '../../../services/meal-service/meal.service';
import { Meal } from '../../../models/meal';
import { DailyProgressService } from '../../../services/daily-progress-service/daily-progress.service';
import { DailyProgress } from '../../../models/daily_progress';
import { DishService } from '../../../services/dish-service/dish.service';
import { Subscription } from 'rxjs/Subscription';


import { Observable } from 'rxjs/Observable';

import 'rxjs/add/observable/zip';


@Component({
  selector: 'app-search-weight',
  templateUrl: './search-weight.component.html',
  styleUrls: ['./search-weight.component.scss']
})

export class SearchComponent implements OnInit, OnDestroy {
  stateMW: string;
  stateMWSearch: string;
  stateMWWeight: string;
  stateEditWeight: string;
  searchProd = '';

  formWeight: FormGroup;
  formWeightEdit: FormGroup;
  mainCalories: number;
  mainProteins: number;
  mainFats: number;
  mainCarbohydrates: number;
  mainWeight: number;

  plus = false;

  userID: string;
  adminID = 'admin';

  productsCommon = [];
  subscription: Subscription;
  productsFavorite: string[];

  meals: Meal[];
  meal = new Meal;
  editMeal = new Meal();

  todayDate: Date;
  currentDate: string;

  favoriteColor: string;

  constructor(public productService: ProductService,
              private mealService: MealService,
              private progressService: DailyProgressService,
              private userService: UserService,
              private dishService: DishService) { }

  ngOnInit() {
    this.stateMW = 'mwClose';
    this.stateMWSearch = 'mwSearchClose';
    this.stateMWWeight = 'mwWeightClose';
    this.stateEditWeight = 'mwWeightClose';
    this.favoriteColor = '#dddddd';

    this.formWeight = new FormGroup({
      name: new FormControl({value: this.meal.name, disabled: true}),
      weight: new FormControl({value: null},
        Validators.required,
        Validators.pattern['^[0-9]+$']
      ),
      calories: new FormControl({value: this.meal.calories, disabled: true}),
      proteins: new FormControl({value: this.meal.proteins, disabled: true}),
      fats: new FormControl({value: this.meal.fats, disabled: true}),
      carbohydrates: new FormControl({value: this.meal.carbohydrates, disabled: true})
    });

    this.formWeightEdit = new FormGroup({
      name: new FormControl({value: this.meal.name, disabled: true}),
      weight: new FormControl({value: null},
        Validators.required,
        Validators.pattern['^[0-9]+$']
      ),
      calories: new FormControl({value: this.meal.calories, disabled: true}),
      proteins: new FormControl({value: this.meal.proteins, disabled: true}),
      fats: new FormControl({value: this.meal.fats, disabled: true}),
      carbohydrates: new FormControl({value: this.meal.carbohydrates, disabled: true})
    });

    this.todayDate = new Date();
    this.currentDate = this.todayDate.toDateString();

    this.subscription = this.userService.loggedUser.subscribe( currentUser => {
      this.userID = currentUser.id;

      this.dishService.getDishes(this.userID).valueChanges().subscribe(dishes => {
        this.productsCommon.push(...dishes);
      });

      this.productService.getProductsConcat(currentUser.id, this.adminID).subscribe(product => {
        product.map(item => this.productsCommon.push(item));
        this.productsCommon.sort((a, b) => {
          if (a.name < b.name) {
            return -1;
          }
          if (a.name > b.name) {
            return 1;
          }
          return 0;
        });
      });

   /* const favs = this.productService.getFavoriteProducts(this.userID);
    const common = this.productService.getProductsCommon(this.adminID);

    Observable.zip(favs, common)
    .subscribe(res => {
      const favIds = res[0];
      const products = res[1];

      this.productsCommon = products.map(product => {
          if (favIds.indexOf(product.id) > -1) { product.favorite = true; }
          return product;
      });
      this.sortProducts();
    });*/

      this.mealService.getMeals(this.userID, this.currentDate).valueChanges().subscribe(meals => {
        meals.sort((a, b) => {
          if (a.dateObject < b.dateObject) {
            return -1;
          }
          if (a.dateObject > b.dateObject) {
            return 1;
          }
          return 0;
        });
        this.meals = meals;
      });
    });
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  sortProducts() {
    const sortFn = (a, b) => {
        if (a.favorite && !b.favorite) {
          return -1;
        }
        if (!a.favorite && b.favorite) {
          return 1;
        }
        if (a.name < b.name ) {
          return -1;
        }
        if (a.name > b.name) {
          return 1;
        }
        return 0;
      };
    this.productsCommon.sort(sortFn);
  }

  open() {
    this.stateMWSearch = 'mwSearchOpen';
    this.stateMW = 'mwOpen';
  }

  chooseProd(product) {
    this.meal.name = product.name;
    this.meal.calories = this.mainCalories = product.calories;
    this.meal.proteins = this.mainProteins = product.proteins;
    this.meal.fats = this.mainFats = product.fats;
    this.meal.carbohydrates = this.mainCarbohydrates = product.carbohydrates;

    this.stateMWSearch = 'mwSearchClose';
    this.stateMWWeight = 'mwWeightOpen';
  }

  close() {
    this.stateMW = 'mwClose';
    this.searchProd = '';
    this.stateMWSearch = 'mwSearchClose';
    this.stateMWWeight = 'mwWeightClose';
    this.stateEditWeight = 'mwWeightClose';
  }

  back() {
    this.stateMWWeight = 'mwWeightClose';
    this.stateMWSearch = 'mwSearchOpen';
  }

  addMeal(meal: Meal) {
    this.mealService.addMealToDB(meal);
    this.progressService.up(this.getProgress(meal));
    this.meal.name = '';
    this.meal.weight = null;
    this.meal.calories = null;
    this.meal.proteins = null;
    this.meal.fats = null;
    this.meal.carbohydrates = null;

    this.stateMW = 'mwClose';
    this.stateMWWeight = 'mwWeightClose';
  }

  countKPCF() {
    if (this.meal.weight !== 100) {
      this.meal.calories = Math.ceil((this.mainCalories * this.meal.weight) / 100);
      this.meal.proteins = Math.ceil((this.mainProteins * this.meal.weight) / 100);
      this.meal.fats = Math.ceil((this.mainFats * this.meal.weight) / 100);
      this.meal.carbohydrates = Math.ceil((this.mainCarbohydrates * this.meal.weight) / 100);
    } else {
      this.meal.calories = this.mainCalories;
      this.meal.proteins = this.mainProteins;
      this.meal.fats = this.mainFats;
      this.meal.carbohydrates = this.mainCarbohydrates;
    }
  }

  getProgress(meal: Meal): DailyProgress {
    const progress = new DailyProgress();
    progress.user_id = this.userID;
    progress.calories = meal.calories;
    progress.proteins = meal.proteins;
    progress.fats = meal.fats;
    progress.carbohydrates = meal.carbohydrates;
    progress.date = Date.now();

    return progress;
  }

  // part EditMeal
  updateMeal(meal: Meal): void {
    this.mealService.updateMealDB(meal);
    this.stateEditWeight = 'mwWeightClose';
    this.stateMW = 'mwClose';

    this.updateProgress(meal, this.plus);
  }

  private updateProgress(meal: Meal, plus: boolean): void {
    if (plus) {
      meal.calories = this.editMeal.calories - this.mainCalories;
      meal.proteins = this.editMeal.proteins - this.mainProteins;
      meal.fats = this.editMeal.fats - this.mainFats;
      meal.carbohydrates = this.editMeal.carbohydrates - this.mainCarbohydrates;
      meal.weight = this.editMeal.weight - this.mainWeight;
      this.progressService.up(this.getProgress(meal));
    } else {
      meal.calories = this.mainCalories - this.editMeal.calories;
      meal.proteins = this.mainProteins - this.editMeal.proteins;
      meal.fats = this.mainFats - this.editMeal.fats;
      meal.carbohydrates = this.mainCarbohydrates - this.editMeal.carbohydrates;
      meal.weight = this.mainWeight - this.editMeal.weight;
      this.progressService.down(this.getProgress(meal));
    }
  }

  openEdit(meal: Meal, id): void {
    this.editMeal.id = id;
    this.editMeal.name = meal.name;
    this.editMeal.weight = this.mainWeight = meal.weight;
    this.editMeal.calories = this.mainCalories = meal.calories;
    this.editMeal.fats = this.mainFats = meal.fats;
    this.editMeal.proteins = this.mainProteins = meal.proteins;
    this.editMeal.carbohydrates = this.mainCarbohydrates = meal.carbohydrates;

    this.stateEditWeight = 'mwWeightOpen';
    this.stateMW = 'mwOpen';
  }

  countEditKPCF(): void {
    this.editMeal.calories = Math.round((this.mainCalories * this.editMeal.weight) / this.mainWeight);
    this.editMeal.proteins = Math.round((this.mainProteins * this.editMeal.weight) / this.mainWeight);
    this.editMeal.fats = Math.round((this.mainFats * this.editMeal.weight) / this.mainWeight);
    this.editMeal.carbohydrates = Math.round((this.mainCarbohydrates * this.editMeal.weight) / this.mainWeight);

    if (this.editMeal.calories > this.mainCalories) {
      this.plus = true;
    }
  }

  favorite(idx: number, val: boolean) {
    event.stopPropagation();
    this.productsCommon[idx].favorite = !this.productsCommon[idx].favorite;
    const prodId = this.productsCommon[idx].id;
    this.productService.switchFavoriteProducts(this.userID, prodId, val);
    this.sortProducts();
  }

}



