
import { Component, OnInit, OnDestroy  } from '@angular/core';
import { NgbModal, ModalDismissReasons, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { FormGroup,  FormBuilder,  Validators, FormControl, NgForm } from '@angular/forms';
import { ProductService } from '../../services/product-service/product.service';
import { UserService } from '../../services/user-service/user.service';
import { Product } from '../../models/product';
import { Subscription } from 'rxjs/Subscription';
import { Router } from '@angular/router';


@Component({
  selector: 'app-modalproductss',
  templateUrl: './modal-products.component.html',
  styleUrls: ['./modal-products.component.scss']
})

export class ModalProductsComponent implements OnInit, OnDestroy  {

  product: Product;
  products: Product[];
  showTitle: boolean;
  weightInit: number;
  submitted: boolean;
  subscription: Subscription;
  subscriptionProduct: Subscription;


  msgs = {
    REQUIRED: 'This field is required',
    VALUE_NUMBER: 'This field must contain positive number'
  };


  constructor( private modalService: NgbModal, private productService: ProductService,
               private userService: UserService, private router: Router  ) {
  }

  ngOnInit() {
    this.product = new Product;
    this.weightInit = 100;
    this.submitted = false;

  }

  createProd(form)  {

    const value = form.value;

    if (form.invalid) {

      return;
    }
    this.submitted = true;
    const weight = value.weight;
    this.product.name = value.productName.toLowerCase();
    this.product.calories = Math.round(+value.kilocalories * weight / 100);
    this.product.proteins = Math.round(+value.proteins * weight / 100);
    this.product.carbohydrates = Math.round(+value.carbohydrates * weight / 100 );
    this.product.fats = Math.round(+value.fats * weight / 100 );

    this.subscription = this.userService.loggedUser.subscribe(user => {
      // adding product only if userService has loggedUser subject
      this.product.user_id = user.id;
      this.close();
      this.subscriptionProduct = this.productService.addProduct(this.product).subscribe(
        res => {
          // add product to products to display all products
          // without need to get products from db each time
          form.reset();
        },
        (e) => console.error(e)
      );
    });
  }

  close() {
    this.router.navigate([{outlets: {popup: null}}]);
  }

  ngOnDestroy() {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }
}



