export class DailyProgress {
  id?: string;
  user_id: string;
  private _date: number;
  private _calories = 0;
  private _proteins = 0;
  private _fats = 0;
  private _carbohydrates = 0;
  private _water = 0;

  set calories(quantity: number) {
    this._calories = this.filter(quantity);
  }

  get calories(): number {
    return this._calories;
  }

  set proteins(quantity: number) {
    this._proteins = this.filter(quantity);
  }

  get proteins(): number {
    return this._proteins;
  }

  set fats(quantity: number) {
    this._fats = this.filter(quantity);
  }

  get fats(): number {

    return this._fats;
  }

  set carbohydrates(quantity: number) {
    this._carbohydrates = this.filter(quantity);
  }

  get carbohydrates(): number {

    return this._carbohydrates;
  }

  set water(quantity: number) {
    this._water = this.filter(quantity);
  }

  get water(): number {

    return this._water;
  }

  set date(unixTime: number) {
    // The date must contain only the year, month, and day values
    // to be stored in the database
    const d = new Date(unixTime);
    this._date = +(new Date(d.getFullYear(), d.getMonth(), d.getDate()));
  }

  get date(): number {

    return this._date;
  }

  // checks the model for emptyness
  isEmpty(): boolean {
    return !this._calories &&
      !this._proteins &&
      !this._fats &&
      !this._carbohydrates &&
      !this._water;
  }

  // increases the model by plus
  add(plus: DailyProgress): DailyProgress {
    this.calories += plus.calories;
    this.proteins += plus.proteins;
    this.fats += plus.fats;
    this.carbohydrates += plus.carbohydrates;
    this.water += plus.water;

    return this;
  }

  // reduces the model by minus
  reduce(minus: DailyProgress): DailyProgress | null {
    if (
      minus.calories > this.calories ||
      minus.proteins > this.proteins ||
      minus.fats > this.fats ||
      minus.carbohydrates > this.carbohydrates ||
      minus.water > this.water
    ) {

      return null;
    }

    this.calories -= minus.calories;
    this.proteins -= minus.proteins;
    this.fats -= minus.fats;
    this.carbohydrates -= minus.carbohydrates;
    this.water -= minus.water;

    return this;
  }

  // filters setting values from undefined and sets accuracy
  private filter(value: number): number {
    if (!value) {

      return 0;
    }

    return +value.toFixed(2);
  }
}



