import { Component, OnInit, OnDestroy } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { FormGroup,  FormBuilder,  Validators, FormControl, NgForm } from '@angular/forms';
import { Dish } from '../../../models/dish';
import { Router } from '@angular/router';
import { Product } from '../../../models/product';
import { ProductService } from '../../../services/product-service/product.service';
import { User } from '../../../models/user';
import { UserService } from '../../../services/user-service/user.service';
import { DishService } from '../../../services/dish-service/dish.service';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'app-create-dish',
  templateUrl: './create-dish.component.html',
  styleUrls: ['./create-dish.component.scss']
})
export class CreateDishComponent implements OnInit, OnDestroy {
  user: User;
  adminID = 'admin';
  itemProd: Product;
  ingredients = [];
  productsCommon = [];

  searchProd = '';

  dish: Dish;
  dishName: string;
  changeContent = true;

  subscription: Subscription;

  msgs = {
    REQUIRED: 'This field is required',
    VALUE_NUMBER: 'This field must contain positive number'
  };

  constructor(private userService: UserService,
              private productService: ProductService,
              private dishService: DishService,
              private modalService: NgbModal,
              private router: Router) { }

  ngOnInit() {
    this.subscription = this.userService.loggedUser.subscribe( currentUser => {
      this.productService.getProductsConcat(currentUser.id, this.adminID)
        .subscribe(product => {
          product.map(item => this.productsCommon.push(item));
          this.productsCommon.sort((a, b) => {
            if (a.name < b.name) {
              return -1;
            }
            if (a.name > b.name) {
              return 1;
            }
            return 0;
          });
        });
    });

    this.dish = new Dish();
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  countNutrient(index, weight) {
    this.itemProd = this.ingredients[index];
    if (weight !== 0 ) {
      this.dish.calories += Math.round((this.itemProd.calories * weight) / 100);
      this.dish.proteins += Math.round((this.itemProd.proteins * weight) / 100);
      this.dish.fats += Math.round((this.itemProd.fats * weight) / 100);
      this.dish.carbohydrates += Math.round((this.itemProd.carbohydrates * weight) / 100);
      this.ingredients[index].weight = +weight;
    }
  }

  createIngredientsArray(product): void {
    if (this.ingredients.includes(product)) {
      this.deleteItem(this.ingredients.indexOf(product));
    } else {
      this.ingredients.push(product);
    }
  }

  deleteItem(index, weight = 0): void {
    this.dish.calories = this.dish.calories - Math.round((this.ingredients[index].calories * weight) / 100);
    this.dish.proteins = this.dish.proteins - Math.round((this.ingredients[index].proteins * weight) / 100);
    this.dish.fats = this.dish.fats - Math.round((this.ingredients[index].fats * weight) / 100);
    this.dish.carbohydrates = this.dish.carbohydrates - Math.round((this.ingredients[index].carbohydrates * weight) / 100);
    this.ingredients.splice(index, 1);
  }

  createDish(form): void {
    if (form.invalid) {
      return;
    }

    const value = form.value;
    const weight = +value.dishWeight;
    const dishAdd = new Dish;
    dishAdd.name = value.dishName.toLowerCase();
    dishAdd.calories = Math.round((this.dish.calories * 100) / weight);
    dishAdd.proteins = Math.round((this.dish.proteins * 100) / weight);
    dishAdd.fats = Math.round((this.dish.fats * 100) / weight);
    dishAdd.carbohydrates = Math.round((this.dish.carbohydrates * 100) / weight);
    dishAdd.ingredients = this.ingredients;
    this.dishService.addDishToDB(dishAdd).subscribe(
      res => {
        if (!res) {
          this.changeContent = false;
        }
      },
      (e) => console.error(e)
    );
    this.close();
  }

  open() {
    this.changeContent = false;
  }

  back() {
    this.changeContent = true;
  }

  close() {
    this.router.navigate([{outlets: {popup: null}}]);
    this.searchProd = '';
  }
}



