import { Component, OnInit, OnDestroy  } from '@angular/core';
import { NgbModal, ModalDismissReasons, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { FormGroup,  FormBuilder,  Validators, FormControl, NgForm } from '@angular/forms';
import { ProductService } from '../../services/product-service/product.service';
import { UserService } from '../../services/user-service/user.service';
import { Product } from '../../models/product';
import { Subscription } from 'rxjs/Subscription';
import { Router } from '@angular/router';

@Component({
  selector: 'app-addproduct',
  templateUrl: './my-products.component.html',
  styleUrls: ['./my-products.component.scss']
})

export class AddProductComponent implements OnInit, OnDestroy {

  products: Product[];
  showMessage: boolean;
  subscription: Subscription;

  constructor(private modalService: NgbModal, private productService: ProductService,
              private userService: UserService, private router: Router) {
  }

  ngOnInit() {
    this.subscription = this.userService.loggedUser.subscribe(currentUser => {
      // getting products only if userService has loggedUser subject
      const subscription = this.productService.getProducts(currentUser.id).subscribe(foundProducts => {
        this.products = foundProducts;
        this.products.length === 0 ? this.showMessage = true : this.showMessage = false;
      });
    });
  }

  deleteProduct(product: Product) {
    this.productService.deleteProduct(product);
  }

  open() {
    this.router.navigate([{ outlets: { popup: 'add-products' }}]);
  }


  ngOnDestroy() {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }

}



