import { Injectable, Inject } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection } from 'angularfire2/firestore';
import { CollectionReference, Query } from '@firebase/firestore-types/index';
import { Observable } from 'rxjs/Observable';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';

import { FirebaseApp } from 'angularfire2';
import * as firebase from 'firebase/app';
import 'firebase/storage';
import 'rxjs/add/operator/do';

import { User } from '../../models/user';


@Injectable()
export class UserService {
  public loggedUser = new BehaviorSubject(new User);
  private userCollection: AngularFirestoreCollection<User>;

  constructor(private afs: AngularFirestore,
              @Inject(FirebaseApp) private firebaseApp: firebase.app.App) {}

  getUser(userEmail: string, userPassword?: string): Observable<User> {
    this.userCollection = this.afs.collection<User>('users', (ref) => {
      let query: CollectionReference | Query = ref;
      if (userEmail) {
        query = query.where('email', '==', userEmail);
      }
      if (userPassword) {
        query = query.where('password', '==', userPassword);
      }

      return query;
    });

    return this.userCollection.valueChanges().map(foundUsers => {
      this.loggedUser.next(Object.assign(new User(), foundUsers[0]));

      return foundUsers[0];
    });
  }

  createUser(newUser: User): Observable<User> {
    // create random id for user
    newUser.id = this.afs.createId();
    if (typeof newUser.kpcf === 'undefined') {
      newUser.calculateKPCF();
    }
    this.loggedUser.next(newUser);

    // afs doesn't create document if .set(data: User)
    const userDoc = this.afs.collection('users').doc<User>(newUser.id);
    userDoc.set(Object.assign({}, newUser));

    return userDoc.valueChanges();
  }

  updateUserData(existedUser: User): Observable<User> {
    this.loggedUser.next(existedUser);

    const userDoc = this.afs.collection('users').doc<User>(existedUser.id);
    userDoc.update(Object.assign({}, existedUser));

    return userDoc.valueChanges().do(user => {
      this.loggedUser.next(Object.assign(new User(), user));
    });
  }

  uploadProfileImage(user, img) {
    const storageRef = this.firebaseApp.storage().ref();

    const path = `profile/${user.id}`;
    const iRef = storageRef.child(path);
    iRef.putString(img, 'base64', {contentType: 'image/png'}).then((snapshot) => {
      iRef.getDownloadURL().then ( (url) => {
        this.afs.doc(`users/${user.id}`).update({photoURL: url});
      });
    });
  }
}



