import { Injectable } from '@angular/core';
import { UserService } from '../user-service/user.service';
import { AngularFirestore, AngularFirestoreCollection } from 'angularfire2/firestore';
import { CollectionReference, Query } from '@firebase/firestore-types/index';
import { User } from '../../models/user';
import { Dish } from '../../models/dish';

@Injectable()
export class DishService {
  user: User;

  constructor(private afs: AngularFirestore, private userService: UserService) { }

  getDishes(userID: string): AngularFirestoreCollection<Dish> {
    return this.afs.collection('dishes', (ref) => {
      let query: CollectionReference | Query = ref;
      if (userID) {
        query = query.where('user_id', '==', userID);
      }
      return query;
    });
  }

  addDishToDB (newDish: Dish) {
    newDish.id = this.afs.createId();
    this.userService.loggedUser.subscribe( res => {
      this.user = res;
    });
    newDish.user_id = this.user.id;
    const dishDoc =  this.afs.collection('dishes').doc(newDish.id);
    dishDoc.set(Object.assign({}, newDish));
    return dishDoc.valueChanges();
  }

  deleteDishFromDB (dish: Dish): void {
    this.afs.doc(`dishes/${dish.id}`).delete();
  }
}



