import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { DishService } from '../../services/dish-service/dish.service';
import { UserService } from '../../services/user-service/user.service';
import { Dish } from '../../models/dish';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'app-dishes',
  templateUrl: './dishes.component.html',
  styleUrls: ['./dishes.component.scss']
})
export class DishesComponent implements OnInit, OnDestroy {
  dishes: Dish[];
  showMessage: boolean;
  showIngredients = false;
  subscription: Subscription;

  constructor(private userService: UserService, private dishService: DishService, private router: Router) { }

  ngOnInit() {
    this.subscription = this.userService.loggedUser.subscribe(currentUser => {
      this.dishService.getDishes(currentUser.id).valueChanges().subscribe(dishes => {
        dishes.sort((a, b) => {
          if (a.name < b.name) {
            return -1;
          }
          if (a.name > b.name) {
            return 1;
          }
          return 0;
        });
        this.dishes = dishes;
        this.showMessage = this.dishes.length === 0;
      });
    });
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  toggleIngredients(dish) {
    this.showIngredients = !this.showIngredients;

    return dish.showIngredients = this.showIngredients;
  }

  openDish() {
    this.router.navigate([{ outlets: { popup: 'create-dish' }}]);
  }

  deleteDish(dish) {
    this.dishService.deleteDishFromDB(dish);
  }
}



