import { AfterViewInit, Component, OnInit } from '@angular/core';
import { UserService } from '../../../services/user-service/user.service';
import { User } from '../../../models/user';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit, AfterViewInit {

  error = false;
  loading = false;
  message: string;
  user: User;
  subscription: Subscription;

  constructor(private userService: UserService, private router: Router ) {
  }

  ngOnInit() {
    this.user = new User;
  }

  checkUserExistence(userExist) {
    this.loading = true;
    if (userExist) {
      this.error = false;
      this.loading = false;
      this.router.navigate(['/main']);
    } else {
      this.error = true;
      this.message = 'Username or password is not right';
      this.loading = false;
    }
  }
  login() {
    this.loading = true;
    if (!this.user.email || !this.user.password) {
      this.error = true;
      this.message = 'Fill in the fields, please';
      this.loading = false;
    } else {
      this.error = false;
      this.subscription = this.userService.getUser(this.user.email, this.user.password)
        .subscribe(
          currentUser => { this.checkUserExistence(currentUser); },
          error => { this.message = 'This is the server error!';
          }
        );
    }
  }

  ngAfterViewInit() {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }
}



