import { Component, OnInit } from '@angular/core';
import { UserService } from '../../services/user-service/user.service';
import { Router } from '@angular/router';
import { User } from '../../models/user';
import { CropperSettings } from 'ng2-img-cropper';
import { Activity } from '../../models/enums/activity';
import { Gender } from '../../models/enums/gender';
import { OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';


@Component({
  selector: 'app-edit-user-data',
  templateUrl: './edit-user-data.component.html',
  styleUrls: ['./edit-user-data.component.scss']
})
export class EditUserDataComponent implements OnInit, OnDestroy {

  user: User;
  submitted: boolean;
  data: any;
  isCropperVisible = false;
  isImageDefault = false;
  cropperSettings: CropperSettings;
  subscription: Subscription;
  activity: Activity;
  activityValues: any;

  msgs = {
    REQUIRED: 'This field is required',
    NOT_POSITIVE: 'This value must be positive'
  };

  constructor(
    private userService: UserService,
    private router: Router) {
  }

  ngOnInit() {
    this.activity = Activity.low;

    this.activityValues = [
      {value: Activity.low, name: 'Low activity'},
      {value: Activity.medium, name: 'Medium activity'},
      {value: Activity.high, name: 'High activity'},
    ];

    this.subscription = this.userService.loggedUser.subscribe(
      (dataUser) => {
        this.user = dataUser;
      });

    // some style for img-cropper element
    this.cropperSettings = new CropperSettings();
    this.cropperSettings.width = 100;
    this.cropperSettings.height = 100;
    this.cropperSettings.croppedWidth = 100;
    this.cropperSettings.croppedHeight = 100;
    this.cropperSettings.canvasWidth = 400;
    this.cropperSettings.canvasHeight = 250;
    this.cropperSettings.rounded = true;
    this.cropperSettings.minWithRelativeToResolution = false;
    this.cropperSettings.cropperDrawSettings.strokeColor = '#C10008';
    this.cropperSettings.cropperDrawSettings.strokeWidth = 2;
    this.cropperSettings.noFileInput = true;
    this.data = {};

    this.submitted = false;
  }

  fileChangeListener($event, cropper) {
    this.isCropperVisible = true;
    const image: any = new Image();
    const file: File = $event.target.files[0];
    const myReader: FileReader = new FileReader();
    myReader.onloadend = function (loadEvent: any) {
      image.src = loadEvent.target.result;
      cropper.setImage(image);
    };
    myReader.readAsDataURL(file);
  }

  acceptChanges(form) {
    if (form.invalid) {

      return;
    }
    this.submitted = true;

    this.saveNewImage();
    const subscription = this.userService.updateUserData(this.user).subscribe(
      () => { this.close(); },
      () => console.log('Something wrong: the database error')
    );
    this.subscription.add(subscription);
  }

  close() {
    this.router.navigate([{outlets: {popup: null}}]);
  }

  saveNewImage() {
    if (this.data && this.data.image) {
      this.userService.uploadProfileImage(this.user, this.data.image.split(/,(.+)/)[1]);
    }
  }

  getDefaultPhoto() {
    this.isImageDefault = true;
    this.user.photoURL = '../../assets/img/' +
      (this.user.gender === Gender.male.valueOf() ? 'man' : 'woman') + '.jpg';

    // with click class='show' appears and animation works,
    // then after 3 seconds 'show' removes from the element's class list
    setTimeout(() => { this.isImageDefault = false; }, 3000);
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }
}



