import { Directive, ElementRef, HostListener, Input } from '@angular/core';
import { NgForm } from '@angular/forms';

@Directive({
  selector: '[appDisplayErrForm]'
})
export class DisplayErrFormDirective {
  form: HTMLFormElement;

  constructor(ref: ElementRef) {
    this.form = ref.nativeElement;
  }

  @HostListener('ngSubmit') onSubmit() {
    if (this.form.classList.contains('ng-invalid')) {
      this.form.classList.add('displayErr');
    }
  }
}



